.Persik {
  display: block;
  width: 100%;
  max-width: 100%;
  margin: 20px auto;
}
.noselect {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}
.menu {
    width: 200px;
    height: 150px;
    overflow: hidden;

}

.menu img {
    width: 115px;
    position: fixed;
    top:97%;
    right: -15px;
    height: 115px;
    margin: -75px 0 0 -100px;
}
.target {
    position: relative;
    margin-top: 2vmin;
    margin-left: 1vmin;
    margin-right: 1vmin;
    width: 0;
    height: 0;
    background: lightblue;
    border-radius: 50%;
    border-style: solid;
    border-width: 10vmin;
    border-top-color: salmon;
    border-right-color: transparent;
    border-bottom-color: salmon;
    border-left-color: transparent;
}

.target img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    /*изменения в стилях*/
    max-height: 14vmin;
    max-width: 14vmin;
}